import React from 'react';
import { color } from '../constants';
import styled from 'styled-components';
import MapIcon from '@material-ui/icons/Place';
import SecureIcon from '@material-ui/icons/Lock';
import DollarIcon from '@material-ui/icons/AttachMoney';
import AnonymousIcon from '@material-ui/icons/HelpOutline';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';

import { useMedia } from '../hooks';

const services = [
  {
    icon: ()=> <AnonymousIcon style={{ color: color.light, fontSize: "4rem" }} />,
    title: 'Anonimo',
    text: 'Coordina tu encuentro de forma discreta.',
  },
  {
    icon: ()=> <SecureIcon style={{ color: color.light, fontSize: "4rem" }} />,
    title: 'Seguro',
    text: 'Confidencialidad en tu encuentro, nuestros perfiles pueden ser seuidos y verificados.',
  },
  {
    icon: ()=> <MapIcon style={{ color: color.light, fontSize: "4rem" }} />,
    title: 'Local',
    text: 'Encuentra lo mejor en tu localidad y zonas aledañas.',
  },
  {
    icon: ()=> <DollarIcon style={{ color: color.light, fontSize: "4rem" }} />,
    title: 'Rentable',
    text: 'genera mas beneficio y rentabilidad con tu actividad.',
  },
]

const ServiceTitle = styled.p`
  margin: 0;
  font-weight: 900;
  color: ${color.light};
  text-align: left;
`
const ServiceInfo = styled.p`
  margin: 0;
  color: ${color.light};
  text-align: ${({ align }) => align};
`
const ServiceImg = styled.div`
  height: 50vh;
  background-color: ${color.prim};
  background-image: url(${require('../images/extra.jpg')});
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  //background-blend-mode: multiply;
`
const ServiceFooter = styled.div`
  height: 15vh;
  background-color: ${color.prim};
  display: flex;
  justify-content: center;
  align-items: center;
`
const FooterTitle = styled(ServiceTitle)`
  text-align: center;
`

export default ()=> {
  const SM = useMedia('sm');
  const MD = useMedia(('md'));
  return(
    <div id="service">
    <Grid container>
      {
        services.map((service, index) => (
          <Grid
            key={service.title}
            item
            xs={12}
            sm={3}
            style={{ backgroundColor: index % 2 === 0 ? color.sec : color.prim }}
          >

            <Container maxWidth="md">
              <Grid container style={{ paddingTop: "2rem", paddingBottom: "2rem" }}>
                <Grid xs={3} sm={12} item container justify="center">
                  {service.icon()}
                </Grid>
                <Grid xs={9} sm={12} item container justify={SM ? "center" : "flex-start"}>
                  <ServiceTitle>{service.title}</ServiceTitle>
                  <ServiceInfo align={ SM && !MD ? 'center' : 'justify' }>{service.text}</ServiceInfo>
                </Grid>
              </Grid>
            </Container>

          </Grid>
        ))
      }
      </Grid>
      <ServiceImg />
      <ServiceFooter>
        <FooterTitle>
          La comunidad mas grande de scrots disponibles para ti
        </FooterTitle>
      </ServiceFooter>
  </div>
)};
