import React, { useEffect, useRef } from 'react';
import { color } from '../constants';
import styled from 'styled-components';
import { keyframes } from 'styled-components';
import heroImg from '../images/hero.jpg';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import ArrowDownIcon from '@material-ui/icons/ArrowDownward';
import { gsap } from 'gsap';

const UpDown = keyframes`
  to{
    transform: translateY(10px);
  }
`

const ArrowCont = styled.a`
  user-select: none;
  display: block;
  text-decoration: none;
  position: absolute;
  bottom: 4rem;
  animation: ${UpDown} 1s linear alternate infinite;
  color: ${color.light};
  font-size: 2rem;
  &:hover{
    color: ${color.prim};
  }
`

const HeroCont = styled.div`
  background-color: rgba(0, 0, 0, .5);
  background-image: url(${heroImg});
  //background-blend-mode: multiply;
  background-size: cover;
  background-position: center center;
  position: relative;
  height: 100vh;
`
const BkgTint = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(0, 0, 0, .5);
  top: 0;
  left: 0;
  //z-index: 100;
`

const MainText = styled.h1`
  font-size: 1rem;
  text-align: center;
  color: ${color.light};
  font-family: {font.title};

`

const SecondText = styled.p`
  width: 80%;
  //font-size: 10px;
  text-align: center;
  color: ${color.light}
`
const BigLogo = styled.h1`
  color: transparent;
  margin: 0;
  -webkit-text-stroke: 2px ${color.phanton};
`

export default ({ router })=> {
  let logoAnimate = useRef(null);
  let backgroundAnimate = useRef(null);
  let infoAnimate = useRef(null);
  let buttonAnimate = useRef(null);
  let arrowAnimate = useRef(null);

  useEffect(()=> {
    const tl = gsap.timeline();
    tl.from(backgroundAnimate, { duration: 2, backgroundColor: 'transparent' })
    .to(logoAnimate, { duration: 4, strokeDashoffset:0, ease: "power1" }, "-=1")
    .from(infoAnimate, { duration: 3, opacity: 0 },"-=3")
    .from(buttonAnimate, { duration: 3, opacity: 0 },"-=2")
    .from(arrowAnimate, { duration: 3, opacity: 0 },"-=2");
    //.to(logoAnimate, { duration: 1, fill: color.prim },"-=2")
    //gsap.to(spicy, 7, { strokeDashoffset:0, ease: "power1", delay: 1 });
  },[backgroundAnimate]);

  return(
    <HeroCont>
      <BkgTint ref={el => { backgroundAnimate = el }}>
      <Container maxWidth="md">
        <Grid container justify="center" alignItems="center" direction="column">
          <h1 style={{ margin: 0 }}>
            <svg width="240px" height="86">
              <text ref={el => { logoAnimate=el }} strokeDasharray="600px" strokeDashoffset="600" id="spicy" fill="none" stroke="#fff" strokeWidth="2px"  fontSize="72px" x="0" y="68">Spicy.</text>
            </svg>
          </h1>
          {/*<MainText>
            Encuentra y ofrece servicios de acompañantes, en tu localidad.
          </MainText>*/}
          <SecondText ref={el=>{infoAnimate=el}}>
            Encuentra tu scort ideal
          </SecondText>
          <Button ref={el => buttonAnimate=el} onClick={()=> router.push('/register')} color="primary" size="small" variant="contained">REGISTRO</Button>
          <ArrowCont href="#about">
            <ArrowDownIcon ref={el => arrowAnimate=el} style={{ fontSize: "2rem" }} />
          </ArrowCont>
        </Grid>
      </Container>
    </BkgTint>
    </HeroCont>
  );
}
