import React, { useState } from 'react';
import styled from 'styled-components';
import { color } from '../constants';
import bkgImg from '../images/extratwice1.jpg';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { useMedia } from '../hooks';

const useStyles = makeStyles((theme) => ({
  root:{
    flexGrow: 1,
  },
}));

const ContactCont = styled.div`
  min-height: ${({ md }) => md ? '50vh' : '100vh'};
  background-color: rgba(0, 0, 0, .5);
  background-image: url(${bkgImg});
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  background-blend-mode: multiply;
  padding: 1.5rem 0;
  display: flex;
  align-items: center;
  overflow: hidden;
`
const ContactTitle = styled.p`
  margin: 0;
  margin-bottom: 1rem;
  font-weight: 900;
  color: ${color.light};
  text-align: left;
  border-bottom: 2px solid ${color.prim};
`
const ContactTitleNoBorder = styled(ContactTitle)`
  border: 0;
  padding: 0;
  margin: 0;
`

const Adress = styled(ContactTitle)`
  font-weight: normal;
  border: 0;
`

const ContactForm = styled.form`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
`
export default ()=> {
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  const classes = useStyles();
  const SM = useMedia('sm');
  const MD = useMedia('md');

  return(
    <ContactCont id="contact" md={SM && !MD ? true : false} >
      <Container maxWidth="lg">
        <Grid
          container
          className={classes.root}
          spacing={5}
        >
          <Grid xs={12} sm={6} item>
            <ContactTitleNoBorder>PUEDES CONTACTARNOS</ContactTitleNoBorder>
            <ContactTitle>POR LOS SIGUIENTES MEDIOS</ContactTitle>
            <Adress>
              Sí tienes alguna sugerencia o inquietud, dejanos tu comentario y te contactaremos
            </Adress>
          </Grid>
          <Grid xs={12} sm={6} item>
            <ContactForm>
              <ContactTitleNoBorder>CORREO</ContactTitleNoBorder>
              <ContactTitle>ELECTRONICO</ContactTitle>
              <TextField
                value={email}
                onChange={e => setEmail(e.target.value)}
                label="Ingresa tu correo"
                variant="filled"
                style={{ background: color.prim, color: color.light, marginBottom: "1rem" }}
              />
              <TextField
                value={message}
                onChange={(e) => setMessage(e.target.value)}
                label="Escribenos tu mensaje"
                variant="filled"
                style={{ background: color.prim, color: color.light, marginBottom: "1rem" }}
                multiline
                rows="4"
              />
              <Button variant="contained" color="primary" style={{ alignSelf: "flex-end", justifySelf: "flex-end", marginTop: "auto" }}>
                Enviar
              </Button>
            </ContactForm>
          </Grid>
        </Grid>
      </Container>
    </ContactCont>
  )
}
